import React from "react";
import img_inicio from '../images/btnNavInicio.png';

const CpBtnInicio = () => {
    return (
        <div className="btn-nav">
            <a href="#top">
                <img src={img_inicio} alt="Voltar para o Incio da Pgina" title="Voltar para o Incio da Pgina" className="btn-nav-topo"/>
            </a>
        </div>
    )
}

// Exporta o Componente para o uso da Aplicao!
export default CpBtnInicio;